import React, { Component } from 'react';
import './App.css';
import Upload from './Upload';

class App extends Component {
  render() {
    return (
      <div className="App">
        <header className="App-header">
          <h1 className="App-title">Link Word</h1>
        </header>
        <div className="UploadRow">
          <Upload target={"template"} description={"Drag your template here"}/>
          <Upload target={"values"} description={"Drag your fill in values here"}/>
        </div>
  
      </div>
    );
  }
}

export default App;



